<div class="dropdown"><a class="dropdown-btn bg-light border rounded d-flex text-decoration-none" id="dropdownMenuLink" data-bs-toggle="dropdown" href="#" role="button" aria-expanded="false" title="選擇地區"><div class="text-muted lh-1 my-auto" v-if="!cityName">目的地</div><div class="text-dark lh-1 my-auto" v-else>{{ cityName }}<a class="text-muted p-2" href="#" title="取消" role="button" @click.prevent="cancelCity"><img src="@/assets/icons/x_icon.svg" width="12" alt="cancel button"></a></div><div class="ms-auto d-flex"><img class="my-auto" src="@/assets/icons/arrow_down_circle.svg" alt="dropdown menu button"></div></a><div class="dropdown-menu border-0 shadow w-100" aria-labelledby="dropdownMenuLink"><div class="row g-2"><div class="col-xl-4 col-lg-6 col-4" v-for="city in cities" :key="city.english"><DropdownButton :city="city" @emit-click="handleClick"></DropdownButton></div></div></div></div>